.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    padding: 8px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    margin-left: auto;
    margin-right: auto;
}

.label {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-bottom: 8px;
}

.label select {
    margin-left: 8px;
    color: #171718;
}

.input {
    color: #171718;
    font-size: 16px;
    min-height: 38px;
    padding: 4px 8px;
    border-radius: 4px;
    border: 1px solid #b3b3b3;
    width: 100%;
    outline: 0;
    transition: border-color 200ms ease-in-out;
    margin-bottom: 16px;
}

.input:focus {
    border-width: 2px;
    border-color: #2684ff;
}